@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i&display=swap);


body {
  background-color: #fff;
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  color: #333;
  font-size: 16px;
  margin: 0;
}

.App {
  display: inline;
}

p {
  margin: 0.3em;
}

a {
  color: #0084ff;
}

#sidebar {
  background-color: #000;
  color: #f4f4f4;

  width: 20%;
  float: left;
  height: 100vh;
  position: fixed;
  padding-top: 20px;
  padding-left: 2.5%;
  padding-right: 2.5%;
  
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

#sidebar > img {
  border-radius: 4px;
  width: 60%;
}

#sidebar h1 {
  font-size: 1.5em;
  font-weight: 500;
  margin-bottom: 0.2em;
}

#sidebar h2 {
  font-size: 1.3em;
  font-weight: 300;
  margin-top: 0.2em;
}

#sidebar ul {
  list-style: none;
  padding: 0;
}

#sidebar li > a{
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0.3em 0;
}

#sidebar li > a > svg{
  margin-right: 0.5em;
}

#sidebar a {
  color: inherit;
}

@media (max-width: 1100px) and (min-width: 769px){
  #sidebar p {
    font-size: 0 !important;
  }

  #sidebar ul {
    display: flex;
  }
}

@media (max-width: 768px) {
  #sidebar {
    width: 100%;
    position: relative;

    padding-top: 0;
    padding-left: 0;
    padding-right: 0;

    align-items: center;
    justify-content: center;
  }

  #sidebar > img {
    border-radius: 4px;
    width: 30%;
  }
}

#main-section {
  width: 70%;
  float: right;
  margin: auto;
}

h2 {
  font-weight: 400;
  font-size: 2em;
}

@media (max-width: 768px) {
  #main-section {
    width: 95%;
    padding-left: 0;
  }
}

a {
  text-decoration: none;
}

.header {
  line-height: 0.5em;
}

.tech-stack {
  font-weight: 600;
}

@media (max-width: 909px) {
  .header {
    line-height: 2em;
  }
}
.card {
  background: white;
  border: 2px solid #ddd;
  border-radius: 10px;
  padding: 5px 20px;
  width: 85%;
  margin: auto;
  margin-top: 2em;
  margin-bottom: 2em;
}

.card h4 {
  font-weight: 600;
}

.header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#summary {
  width: 80%;
  margin: auto;
  margin-top: 3em;
  margin-bottom: 3em;
  font-size: 1.2em;
}

#summary p {
  margin: 1em 0;
}

.education {
  line-height: 1.4em;
  display: inline;
}

.education-details {
  display: flex;
}

.education-info {
  width: 70%;
}

.uni, #coursework {
  font-weight: 600;
  margin-bottom: 0;
}

#degree {
  font-weight: 600;
  margin-top: 0;
}

@media (max-width: 768px) {
  .education {
    display: block;
  }

  .education-info {
    width: 100%;
  }

  .education-img {
    display: none;
  }
}

